import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";
import {
  Container,
  CssBaseline,
  Typography,
  Box,
  CircularProgress,
} from "@mui/material";
import { onAuthStateChanged } from "firebase/auth";
import { auth } from "./firebase";
import Inputs from "./components/Inputs";
import TopButtons from "./components/TopButtons";
// import TimeAndLocation from "./components/TimeAndLocation";
import TimeAndLocation from "./TimeAndLocation";
import TemperatureAndDetails from "./components/TemperatureAndDetails";
import Forecast from "./components/Forecast";
// import SignUp from "./components/SignUp";
import SignUp from "./components/SingUp";
// import Login from "./components/Login";
import Login from "./components/LogIn";
import Logout from "./components/Logout";
// import { getWeatherData } from "./services/weatherService";
import { getWeatherData } from "./components/services/weatherService";

function App() {
  const [weatherData, setWeatherData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [city, setCity] = useState("New York");
  const [user, setUser] = useState(null);

  useEffect(() => {
    onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });
  }, []);

  useEffect(() => {
    const fetchWeatherData = async () => {
      setLoading(true);
      try {
        const data = await getWeatherData(city);
        setWeatherData(data);
      } catch (error) {
        console.error("Error fetching weather data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchWeatherData();
  }, [city]);

  const handleCityChange = (newCity) => {
    setCity(newCity);
  };

  return (
    <Router>
      <Container maxWidth="md">
        <CssBaseline />
        <Box my={4}>
          <Typography variant="h2" align="center" gutterBottom>
            Weather App
          </Typography>
          <Routes>
            <Route
              path="/"
              element={
                user ? <Navigate to="/dashboard" /> : <Navigate to="/login" />
              }
            />
            <Route
              path="/login"
              element={user ? <Navigate to="/dashboard" /> : <SignUp />}
            />
            <Route
              path="/signup"
              element={user ? <Navigate to="/dashboard" /> : <Login />}
            />
            <Route
              path="/dashboard"
              element={
                user ? (
                  <>
                    <Logout />
                    <TopButtons onCityChange={handleCityChange} />
                    <Inputs onCityChange={handleCityChange} />
                    {loading ? (
                      <Box display="flex" justifyContent="center" my={4}>
                        <CircularProgress />
                      </Box>
                    ) : (
                      <>
                        <TimeAndLocation data={weatherData} />
                        <TemperatureAndDetails data={weatherData} />
                        <Forecast data={weatherData} />
                      </>
                    )}
                  </>
                ) : (
                  <Navigate to="/singup" />
                )
              }
            />
          </Routes>
        </Box>
      </Container>
    </Router>
  );
}

export default App;
