import React from 'react';
import { Box, Grid, Typography, Paper } from '@mui/material';

function Forecast({ data }) {
  if (!data) return null;

  // Dummy data for illustration; you would replace this with real forecast data
  const forecast = [
    { day: 'Mon', temp: '24C', description: 'Sunny' },
    { day: 'Tue', temp: '22C', description: 'Cloudy' },
    { day: 'Wed', temp: '20C', description: 'Rainy' },
    { day: 'Thu', temp: '26C', description: 'Sunny' },
    { day: 'Fri', temp: '28C', description: 'Hot' }
  ];

  return (
    <Box my={4}>
      <Grid container spacing={2} justifyContent="center">
        {forecast.map((item) => (
          <Grid item xs={12} sm={4} md={2} key={item.day}>
            <Paper elevation={3} style={{ padding: '16px', textAlign: 'center' }}>
              <Typography variant="h6">{item.day}</Typography>
              <Typography variant="body1">{item.temp}</Typography>
              <Typography variant="body2">{item.description}</Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
}

export default Forecast;