import React from 'react';
import { Box, Grid, Typography } from '@mui/material';

function TemperatureAndDetails({ data }) {
  if (!data) return null;

  const { main: { temp, feels_like, humidity }, wind: { speed } } = data;

  return (
    <Box my={4}>
      <Grid container spacing={2} justifyContent="center">
        <Grid item xs={12} sm={6}>
          <Typography variant="h2" align="center">{temp}C</Typography>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="body1" align="center">Feels like: {feels_like}C</Typography>
          <Typography variant="body1" align="center">Humidity: {humidity}%</Typography>
          <Typography variant="body1" align="center">Wind: {speed} km/h</Typography>
        </Grid>
      </Grid>
    </Box>
  );
}

export default TemperatureAndDetails;